.sidebar {
    display: none;
}

.sidebar.open {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    width: 317px;
    height: 720px;
    z-index: 9999;
    background: #FFF;
    gap: 23px;

}


.background {
    position: absolute;
    width: 1280px;
    height: 720px;
    background-color: #000;
    opacity: 0.3;
}

.nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 45px;
}

.nav a {

    text-decoration: none;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    color: #000;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a.active {
    color: #0059B2;
    text-decoration-line: underline;
}
.close {
    margin-right: 15px;
    border: none;
    background: none;
    padding: 0;
    cursor: pointer;
    align-self: flex-end;

}

